<template>
  <div class="avatar-setting">
    <van-nav-bar fixed title="用户设置" left-arrow left-text="返回" @click-left="$router.back()"></van-nav-bar>
    <vue-cropper ref="cropper" :src="avatar" v-if="avatar" class="avatar"></vue-cropper>
    <van-image class="avatar" fit="contain" :src="$store.user.avatar" v-else />
    <input type="file" v-show="false" ref="file" @change="fileSelect">
    <van-button size="large" type="info" @click="$refs.file.click()">选择图片</van-button>
    <van-button size="large" type="primary" @click="crop">裁剪上传</van-button>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        avatar: '',
        avatarType: ''
      }
    },
    methods: {
      fileSelect() {
        let file = this.$refs.file.files[0]
        let reader = new FileReader()
        reader.onload = e => {
          // this.$refs.cropper.clear()
          // this.$refs.cropper.replace(e.target.result)
          this.avatar = e.target.result
        }
        if (file.type.indexOf('image') > -1) {
          this.avatarType = file.type
          reader.readAsDataURL(file)
        } else {
          this.$Toast('请选择正确的图片格式')
        }
      },
      crop() {
        // this.$refs.cropper.getCroppedCanvas().toDataURL(this.avatarType, 1)
        this.$refs.cropper.getCroppedCanvas().toBlob(blob => {
          let formData = new FormData()
          formData.append('avatar', blob)
          this.$axios.post(`/user/avatar`, formData).then(res => {
            if (res.errcode == 0) {
              this.$Toast('上传头像成功')
              this.$store.user.avatar = res.data.avatar
              this.$router.back()
            }
          })
        })

      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .avatar-setting {
    padding-top: 46px;
  }

  .avatar {
    /* max-width: 100%; */
    padding: 10px;
    width: 100vw;
    height: calc(100vh - 150px);
  }
</style>


